<script>
import OptinArticles from "@/components/cmptsOptin/OptinArticles.vue"
export default {

  name:'optinFirst',
   props: {
    pickUpArticles: {
      type: Array,
      default: [],
    }
  },
    components: {
    OptinArticles,
  },
  data() {
    return {
      pickUpLifeStyleArticles:[
        {
          articleId: 310000,
          articleImg: "https://news2.medy-id.jp/htdocs/images/common/comment.png",
          //TODO CHECK SITE? OR TYPE?
          articleTypeName: "1．コメント機能で発言する",
          articleTitle: "日々の医療ニュースや臨床Q&Aに、コメントすることが出来るようになりました。 医師や医療従事者ならではの視点で、思うことを自由に発信してください。"
        },
        {
          articleId: 310001,
          articleImg: "https://news2.medy-id.jp/htdocs/images/common/heart.png",
          //TODO CHECK SITE? OR TYPE?
          articleTypeName: "2．「ありがとう」をもらう・送る",
          articleTitle: "自身のコメントを他のユーザーが参考になったと感じた場合「ありがとう」を 送られることがあります。 送られた「ありがとう」は貯まっていき、その人がMEDY内で どれだけ他のユーザーの助けになっているかを表す指標となります。 また、ありがとうを送ったりもらったりすると プレゼントに応募できる「MEDYチケット」が貯まります。"
        },
        {
          articleId: 310002,
          articleImg: "https://news2.medy-id.jp/htdocs/images/common/gift.png",
          //TODO CHECK SITE? OR TYPE?
          articleTypeName: "3．プレゼントに応募する",
          articleTitle: "貯まった「MEDYチケット」でプレゼントに応募する事ができます。プレゼントは、コンビニやカフェの商品と交換できる手軽なものから 数千円～数万円相当のものまで様々なラインナップを取り揃えております	プレゼントは、コンビニやカフェの商品と交換できる手軽なものから数千円～数万円相当のものまで様々なラインナップを取り揃えております。"
        },
      ],
      medical: '',
            options: [
                { text: '医療ニュース', value: 'apple' },
                { text: '臨床Q&A', value: 'banana' },
                { text: '医療ニュースや臨床Q&A', value: 'watermelon' }
            ],
      commentDisabled:true,
      thankDisabled:false,
      presentDisabled:false,

      commentStyle:true,
      thankStyle:false,
      presentStyle:false,
      
      nameOn:false,
      passNameOn:false,
      nameLogStyle:true,
      nameIsDisabled:true,
      nameInputStyle:false,
      passwInfoIsdisabled:true,
      passwLogStyle:false,
      passInputStyle:false,

      dotIndex: 1,
      touchStartPosX: 0,
      touchStartPosY: 0,
      diff: 0,
      prevDiff: 0,
      isTouchIngY: false,
      isMoving: false,
      
    }
  },
  watch: {
    diff: function(){
      let winWid = window.innerWidth;
      if(this.diff == 0){
        this.dotIndex = 1;
      } else if(this.diff == -winWid){
        this.dotIndex = 2;
      } else if(this.diff == -winWid*2){
        this.dotIndex = 3;
      }
    }
  },
  methods: {
    clickComment:function(){
      this.commentStyle=true
      this.thankStyle=false
      this.presentStyle=false
      this.commentDisabled=true
      this.thankDisabled=false
      this.presentDisabled=false
    },
    clickThank:function(){
      this.commentStyle=false
      this.thankStyle=true
      this.presentStyle=false
      this.thankDisabled=true
      this.commentDisabled=false
      this.presentDisabled=false
    },
     clickPresent:function(){
      this.presentDisabled=true
      this.presentStyle=true
      console.log(this.presentStyle)
      this.thankStyle=false
      this.commentStyle=false
      this.commentDisabled=false
      this.thankDisabled=false
    },
    selectNameOk:function(){
      this.nameOn=true
      this.passNameOn=false
      this.nameLogStyle=false
      this.nameIsDisabled=false
      this.nameInputStyle=true
      this.passwInfoIsdisabled=true
      this.passwLogStyle=false
      this.passInputStyle=false
      this.$refs.passInputId.value=""
      
    },
    selectPassOk:function(){
      this.passNameOn=true
      this.nameOn=false
      this.nameLogStyle=true
      this.nameInputStyle=false
      this.nameIsDisabled=true
      this.passwInfoIsdisabled=false
      this.passwLogStyle=true
      this.passInputStyle=true
      this.$refs.nameInputId.value=""
    },
    init: function () {
      let windowWid = window.innerWidth;
      this.$refs.pickUpInSideArea.style.width = this.pickUpArticles.length * windowWid + 'px'; 
      this.diff = -windowWid * (this.dotIndex - 1);
      this.$refs.pickUpInSideArea.style.transition = 'all .8s ease';
    },
    // pickuparticle
    getInitPos: function(event){
      this.isMoving = true;
      this.touchStartPosX = event.targetTouches[0].pageX;
      this.touchStartPosY = event.targetTouches[0].pageY;
      if(this.prevDiff != 0 && this.prevDiff != window.innerWidth && this.prevDiff != window.innerWidth*2){
          this.prevDiff = -window.innerWidth * (this.dotIndex - 1);
      }
      this.$refs.pickUpInSideArea.style.transition = '';
      if(!this.$store.state.isForbiddenPullDown){
        this.$store.commit('makeScrollForbiddenPullDown');
      }
    },
    moveArticle: function(event){
      if(this.isTouchIngY){
        this.diff = this.prevDiff;
        return;
      }
      if (Math.abs(event.touches[0].pageY - this.touchStartPosY) > 50 && Math.abs(event.touches[0].pageX - this.touchStartPosX) < 50) {
        if(this.$store.state.isForbiddenPullDown){
          this.$store.commit('makeScrollCanPullDown');
        }
        this.diff = this.prevDiff;
        this.isTouchIngY = true;
      } else {
        let widWid = window.innerWidth;
        if(this.prevDiff == 0 && event.touches[0].pageX - this.touchStartPosX > 0){
          this.diff = 0;
        } else if(this.prevDiff == -this.$refs.pickUpScrollArea.clientWidth*(this.pickUpArticles.length - 1) && event.touches[0].pageX - this.touchStartPosX < 0){
          this.diff = this.prevDiff;
        } else if(event.touches[0].pageX - this.touchStartPosX < -widWid){
          this.diff = this.prevDiff - widWid;
        } else if(event.touches[0].pageX - this.touchStartPosX > widWid){
          this.diff = this.prevDiff + widWid;
        }else {
          this.diff = this.prevDiff + event.touches[0].pageX - this.touchStartPosX;
        }
      }
    },
    toggleArticle: function(){
      this.$refs.pickUpInSideArea.style.transition = 'all .8s ease';
      if (!this.$store.state.isForbiddenPullDown) {
        this.isMoving = false;
        return;
      }
      this.$store.commit('makeScrollCanPullDown');
      this.isTouchIngY = false;
      
      if((this.prevDiff == 0 && this.diff < -50) || (this.prevDiff == -this.$refs.pickUpScrollArea.clientWidth*2 && this.diff > this.prevDiff + 50)){
        this.diff = -this.$refs.pickUpScrollArea.clientWidth;
      } else if(this.prevDiff == -this.$refs.pickUpScrollArea.clientWidth && this.diff < this.prevDiff-50){
        this.diff = -this.$refs.pickUpScrollArea.clientWidth * 2;
      } else if(this.prevDiff == -this.$refs.pickUpScrollArea.clientWidth && this.diff > this.prevDiff + 50){
        this.diff = 0;
      } else {
        this.diff = this.prevDiff;
      }
      this.prevDiff = this.diff;
      this.isMoving = false;
    },
    nativPickUpArticle: function(index){
      this.dotIndex = index;
      this.diff = -(index - 1) * this.$refs.pickUpScrollArea.clientWidth;
    },
    gotoArticleDetail: function (id) {
      this.$emit('setListScrollTop');
      this.$router.push({ name: 'articleDetail', params: { articleId: id } });
    }
  }
}
</script>
